@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,600;1,700&display=swap');

:root {
  --purple: rgb(112, 40, 120);
  --yellow: rgb(219, 200, 0);
  --nd-dark: rgb(59, 62, 80);
  --nd-light: rgb(224, 225, 231);
  --nd-highlight: rgb(100, 103, 127);
  --nd-link: rgb(149, 156, 201);
}

.App {
  
  background-color: rgb(59, 62, 80);
  color: rgb(224, 225, 231); 
  min-height: 100vh;
  font-family: 'Josefin Sans', Arial, Helvetica, sans-serif;
}

.divider {
  color: rgb(224, 225, 231);  
  background-color:rgb(224, 225, 231);
  width: 40vw;
  margin: 24px auto;
  border: none;
  height: 2px !important;
  opacity: .5 !important;
  border-radius: 1px;
}

.my-bg-purple {
  background-color: var(--purple);
}

.my-bg-yellow {
  background-color: var(--yellow);
}




@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
