.nav-link {
  line-height: 2rem;
  box-sizing: border-box !important;
  border-top: 2px solid transparent;
  border-bottom: 2px solid transparent;
}

.navbar {
  padding-top: 0;
  padding-bottom: 0;
}

.nav-link:hover {
  background-color: var(--nd-dark);
  color: var(--nd-light) !important;
  border-bottom: 2px solid var(--nd-light);
}

.active-nav-link {    
  background-color: var(--nd-dark) !important;
  color: var(--nd-light) !important;
  border-bottom: 2px solid var(--nd-light) !important;
}