.body {
  color: rgb(224, 225, 231);
  font-size: 1rem;
  font-weight: 300;
  padding-bottom: 24px;
  min-height: 90vh;
}

.name-header {
  font-size: 6rem;
  color: azure;
  font-weight: 100;
  letter-spacing: 0.6rem;
  justify-content: center;
  padding: 12px 0;
  text-align: center;
}

.photo {
  width: 100%;
  border-radius: 50%;
}

.intro-text {
  display: flex;
  flex-direction: column;
  justify-content: end;
  line-height: 1.7;
}

.social-icon {
  font-size: 3rem;
}

.social-icon a {
  text-decoration: none !important;
  color: inherit !important;
}

.social-icon a:hover {
  color: var(--nd-link) !important;
}
